import React from 'react';
import { connect } from "react-redux";
import { Slidersettings } from "../../redux/constant";
import { Search } from "../../redux/actions";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
function Movies(props) {
    const Moveis = props.SearchMovies.movies.map((v, k) => {
        return (
            <div className="Poster" key={k}>
                <img src={`./img/cover/${v}.png`} alt="" />
            </div>
        )
    })
    const setting = {
        ...Slidersettings,
        slidesToShow: 5,
        centerPadding: "0px",
        autoplay: true,
        arrows: false,
        responsive: [
            {
                breakpoint: 1024,
                settings: {
                    slidesToShow: 5,
                    slidesToScroll: 1,
                    infinite: true,
                    dots: true
                }
            },
            {
                breakpoint: 768,
                settings: {
                    slidesToShow: 3,
                    slidesToScroll: 1,
                    initialSlide: 2
                }
            },
            {
                breakpoint: 480,
                settings: {
                    slidesToShow: 2,
                    slidesToScroll: 1
                }
            }
        ]

    }
    return (
        <React.Fragment>
            <div className="container-fluid" id="MoviesSearch">
                <div className="row">
                    <div className="col-12">
                        <h6>Search</h6>
                    </div>
                    <div className="col-12 InputSearch">
                        <div className="content">
                            <i className="fas fa-search">
                            </i>
                            <input type="text" placeholder="Search Movies, Tv Shows, Trailers...." />
                            <div className="Dropdown">
                                <select>
                                    <option>Chose Genres</option>
                                </select>
                                <i className="fas fa-chevron-down d-md-flex d-none"></i>
                            </div>
                        </div>
                    </div>
                    <div className="col-12 Movies">
                        <div className="Heading">
                            <h6>Recently Played</h6>
                        </div>
                        <Slider {...setting}>
                            {Moveis}
                        </Slider>
                    </div>
                    <div className="col-12 Movies">
                        <div className="Heading">
                            <h6>Recently Played</h6>
                        </div>
                        <Slider {...setting}>
                            {Moveis}
                        </Slider>
                    </div>
                </div>
            </div>
        </React.Fragment>
    )
}
const mapStateToProps = state => {
    return {
        ...state
    }
}
const mapDispatchToProps = dispatch => {
    return {
        Searhing: e => {
            dispatch(Search.onMovieSearch(e))
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Movies);