import React from "react";
import { Slidersettings } from "../../redux/constant";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
function Trending() {
  const List = [1, 2, 3, 4, 5, 6];
  const Movie = List.map((v, k) => {
    return (
      <div className="col-12 MovieContainer" key={k}>
        <div className="Movie w-100">
          <div className="img">
            <img src={`./img/cover/${v}.png`} alt="" />
          </div>
          <div className="Detail">
            <p>Untitled Movie</p>
            <span>The Genre</span>
            <span>
              <b>2018</b>
            </span>
          </div>
        </div>
      </div>
    );
  });
  const setting = {
    ...Slidersettings,
    vertical: true,
    verticalSwiping: true,
    slidesToScroll: 1,
    centerMode: false,
    autoplay: true,
    swipeToSlide: true,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 768,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          initialSlide: 2
        }
      },
      {
        breakpoint: 480,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1
        }
      }
    ]
  };
  return (
    <React.Fragment>
      <div className="container-fluid" id="Trending">
        <div className="row">
          <div className="col-12">
            <h6>Trending</h6>
          </div>
          <Slider {...setting}>{Movie}</Slider>
        </div>
      </div>
    </React.Fragment>
  );
}
export default Trending;
